<template>
  <div>
    <div class="alert alert-primary text-center">Recadastramento</div>
    <div class="container">
      <!-- Recadastrar -->
      <div class="card mb-2" @click="iniciarRecadastramento">
        <div class="card-body p-2 d-flex align-items-center">
          <img src="../../assets/icon/new_document.svg" alt="" style="width: 3rem; max-height: 3rem">
          <div class="flex-fill pl-2 ml-2 border-left text-center">
            <h6 class="mb-0 text-uppercase">Recadastrar</h6>
            <p class="mb-0 small">atualize seus dados cadastrais</p>
          </div>
        </div>
      </div>
      <!-- Consultar histórico -->
      <div class="card mb-2" @click="$router.push({name: 'recad-historico'})">
        <div class="card-body p-2 d-flex align-items-center">
          <img src="../../assets/icon/open-book.svg" alt="" style="width: 3rem; max-height: 3rem">
          <div class="flex-fill pl-2 ml-2 border-left text-center">
            <h6 class="mb-0 text-uppercase">Histórico</h6>
            <p class="mb-0 small">recadastramentos realizados pelo App</p>
          </div>
        </div>
      </div>
    </div>
    <div v-if="loading"
         class="position-absolute vw-100 vh-100 d-flex justify-content-center align-items-center overflow-hidden"
         style="background-color: rgba(255, 255, 255, .6); top: 0; z-index: 9">
      <div class="spinner-border text-primary" role="status"></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Menu",
    data: () => ({
      loading: false
    }),
    methods: {
      async iniciarRecadastramento() {
        //=== Navega para a tela de recadastramento ===
        try {
          this.loading = true;
          // Tenta atualizar o cadastro antes de proceder
          await this.$store.dispatch('updateUserData');
          await this.$router.push({name: 'recad-form-dadospessoais'});
        } catch (e) {
          this.$toastr.error('Não foi possível obter seus dados cadastrais, verifique sua conexão com a internet.');
        } finally {
          this.loading = false;
        }
      }
    }
  }
</script>

<style scoped>
  .card {
    cursor: pointer;
  }
  .card:hover, .card:focus {
    z-index: 1;
    color: #fff;
    text-decoration: none;
    background-color: #007bff;
  }
  .card:hover h6, .card:focus h6{
    color: #fff;
  }
</style>
